.inputContainer{
    display: flex;
    justify-content: flex-end;
    position: relative;
    padding-right: 15px;
  }
  
  .formContainer #inputCiudad {
    border: 0;
    border-radius: 5px;
    background-color: #3f3f57;
    position: relative;
    padding-left: 10px;
    right: 7px;
    top: 3px;
    height: 34px;
  }
  
  .formContainer #inputCiudad:focus {
    outline:none;
    background-color: #ffff;
    transition: 1.5s;
  }